<template>
  <HelloWorld />
  <Footer />
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import Footer from "./components/Footer.vue"
import  "./assets/style.css"
import "./assets/eat.png"
import $ from "jquery"

export default {
  mounted(){
    window.addEventListener('load', () => {
         var elements= document.getElementById("body").getElementsByClassName("collapse")
         elements[0].classList.toggle("show")
         console.log(document.getElementById("body").getElementsByClassName('nav-link')[0].setAttribute("aria-expanded","true"))
         $('#body > [data-toggle="collapse"]').on('click',function(e){
         if ( $(this).parents('.accordion').find('.collapse.show') ){
         var idx = $(this).index('[data-toggle="collapse"]');
         if (idx == $('.collapse.show').index('.collapse')) {
            // prevent collapse
            e.stopPropagation();
        }
    }
});
    })
  },
  
  name: "App",
  components: {
    HelloWorld,
    Footer
  },
  
};

myFunction();
var myVar;
function myFunction() {
  console.log(myVar)
  myVar = setTimeout(showPage, 3000);
}

function showPage() {
  document.getElementById("loader").style.display = "none";
  document.getElementById("app").style.display = "block";
}
</script>

